export const deletePaymentInfo = createAsyncThunk(
  "PaymentSlice/deletePaymentInfo",
  async (payload, { rejectWithValue }) => {
    let result = null;
    try {
      result = await axios.delete(`http://localhost:3001/res_info/${payload?.id}`);
    
    } catch (err) {
      result = rejectWithValue(err.message);
    
    
    }
    return result;
  }
);
const PaymentSlice = createSlice({
  name: "PaymentSlice",
  initialState: {
    data: null,
    loading: false,
    error: null,
  },
  reducers: {},
  extraReducers: {
    [getPayment.pending]: (state, { payload }) => {
      return { ...state, loading: true };
    },
    [getPayment.fulfilled]: (state, { payload }) => {
      return {
        data: payload?.data,
        loading: false,
        error: null,
      };
    },
    [getPayment.rejected]: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: {
          code: payload?.data?.rt
            ? payload?.data?.rt
            : payload?.status
            ? payload.status
            : 500,
          message: payload?.data?.rtmsg
            ? payload?.data?.rtmsg
            : payload?.statusText
            ? payload.statusText
            : "Server Error",
        },
      };
    },
    [getPaymentInfo.pending]: (state, { payload }) => {
      return { ...state, loading: true };
    },
    [getPaymentInfo.fulfilled]: (state, { payload }) => {
      return {
        data: payload?.data,
        loading: false,
        error: null,
      };
    },
    [getPaymentInfo.rejected]: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: {
          code: payload?.data?.rt
            ? payload?.data?.rt
            : payload?.status
            ? payload.status
            : 500,
          message: payload?.data?.rtmsg
            ? payload?.data?.rtmsg
            : payload?.statusText
            ? payload.statusText
            : "Server Error",
        },
      };
    },
    [postPaymentInfo.pending]: (state, { payload }) => {
      return { ...state, loading: true };
      
    },
    [postPaymentInfo.fulfilled]: (state, { payload }) => {
      const data = cloneDeep(state.data);
      
      console.log(data);
      

      return {
        data: data,
        loading: false,
        error: null,
      };
    },
    [postPaymentInfo.rejected]: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: {
          code: payload?.data?.rt
            ? payload?.data?.rt
            : payload?.status
            ? payload.status
            : 500,
          message: payload?.data?.rtmsg
            ? payload?.data?.rtmsg
            : payload?.statusText
            ? payload.statusText
            : "Server Error",
        },
      };
    },
    [deletePaymentInfo.pending]: (state, { payload }) => {
     

      return { ...state, loading: true };
    },
    [deletePaymentInfo.fulfilled]: (state, { meta, payload }) => {
      const data = cloneDeep(state.data);
      

      const index = data.findIndex(
        (element) => element.merchant_uid === meta.arg.merchant_uid
      
        );
      if (index !== undefined) {
        data.splice(index, 1);
      }
      console.log(state.data);
      return {
        data: data,
        loading: false,
        error: null,
      };
    },
    [deletePaymentInfo.rejected]: (state, { payload }) => {
      
      return {
        ...state,
        loading: false,
        error: {
          code: payload?.data?.rt
            ? payload?.data?.rt
            : payload?.status
            ? payload.status
            : 500,
          message: payload?.data?.rtmsg
            ? payload?.data?.rtmsg
            : payload?.statusText
            ? payload.statusText
            : "Server Error",
        },
      };
    },
  },
});

export default PaymentSlice.reducer;
