import React, { memo } from "react";
import { Link } from "react-router-dom";
import useAxios from "axios-hooks";

import regexHelper from "../../libs/RegexHelper";

import styled from "styled-components";

const SigninContainer = styled.form`
  width: 460px;
  margin: 0 auto;
  padding-bottom: 100px;

  h2 {
    text-align: center;
    padding: 60px 0 20px 0;
    font-size: 44px;
    font-weight: 700;
  }

  .info {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;

    label {
      margin: 19px 0 8px 0;
      font-size: 14px;
      font-weight: 700;
    }

    input {
      padding: 10px 110px 10px 14px;
      border: 1px solid rgb(218, 218, 218);
    }

    .info-rrn {
      margin-top: 19px;
      margin-bottom: 8px;

      div {
        display: flex;
        align-items: center;
        margin-top: 8px;

        input {
          width: 100%;
        }

        p {
          margin: 0 20px;
        }
      }
    }
  }

  button {
    width: 100%;
    padding: 15px 0;
    border: none;
    border-radius: 3px;
    background-color: #000;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
  }
`;

const Signin = memo(() => {
  //     Ajax   
  const [{ loading }, refetch] = useAxios(
    {
      url: "http://localhost:3001/members",
      method: "POST",
    },
    { manual: true }
  );

  const onSubmit = (e) => {
    e.preventDefault();

    //    
    const current = e.target;

    //    
    try {
      //  
      regexHelper.value(current.id, " . ()");
      regexHelper.idCheck(
        current.id,
        5,
        20,
        "5~20  ,  (_),(-)  ."
      );

      //  
      regexHelper.value(current.pwd, " . ()");
      regexHelper.pwdCheck(
        current.pwd,
        8,
        16,
        "8~16   , ,  ."
      );
      regexHelper.pwdCompareTo(
        current.pwd,
        current.pwdCheck,
        "  ."
      );

      //  
      regexHelper.value(current.name, " . ()");
      regexHelper.nameCheck(
        current.name,
        "    . (,   )"
      );

      //  
      // regexHelper.yearCheck(
      //   current.year,
      //   "  4  ."
      // );

      //  
      regexHelper.emailCheck(current.email, "   .");

      //  
      regexHelper.value(current.tel, " . ()");
      regexHelper.phoneCheck(current.tel, "   .");
    } catch (e) {
      window.alert(e.message);
      e.field.focus();

      return;
    }

    let json = null;

    //   
    const id = current.id.value; // 
    const pwd = current.pwd.value; // 
    const name = current.name.value; // 
    const tel = current.tel.value; // 
    const email = current.email.value; // 

    (async () => {
      try {
        const response = await refetch({
          data: {
            id: id,
            pwd: pwd,
            name: name,
            tel: tel,
            email: email,
          },
        });

        json = response.data;
      } catch (e) {
        console.error(e);
        window.alert(
          `[${e.response.status} : ${e.response.statusText}\n${e.message}]`
        );
      }

      if (json != null) {
        window.alert(".");
      }
    })();
  };

  return (
    <SigninContainer onSubmit={onSubmit}>
      <h2></h2>
      <div className="info">
        <label htmlFor="id"></label>
        <input type="text" name="id" placeholder="" />

        <label htmlFor="pwd"></label>
        <input type="password" name="pwd" placeholder="" />
        <label htmlFor="pwdCheck" placeholder=" ">
           
        </label>
        <input type="password" name="pwdCheck" />

        <label htmlFor="name"></label>
        <input type="text" name="name" placeholder="" />

        <div className="info-rrn">
          <label htmlFor=""></label>
          <div>
            <input type="text" />
            <p>-</p>
            <input type="text" />
          </div>
        </div>
        <label htmlFor="tel"></label>
        <input type="text" name="tel" placeholder="" />

        <label htmlFor="email"></label>
        <input type="email" name="email" placeholder="" />
      </div>
      {/* <Link to="/TicketingPage/SigninConfirm"> */}
      <button type="submit"></button>
      {/* </Link> */}
    </SigninContainer>
  );
});

export default Signin;
